@import url(https://fonts.googleapis.com/css2?family=Slabo+13px&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#444444}.navigation-wrapper .link-wrapper .home{background-color:#ef7c1a}.introduction{margin-top:20vh;width:40%;margin-left:10%;margin-bottom:25vh;color:white}.introduction h1{padding-bottom:1.2rem;font-family:"Slabo 13px", serif;font-size:36px}.introduction p{font-family:"Slabo 13px", serif;font-size:18px;margin-bottom:12px}.introduction .buttons{display:flex;align-content:space-between;width:100%}.introduction .buttons a{border-radius:50px;width:80%;margin:10px 10px;padding:20px 10px;background-color:#ef7c1a;text-align:center;font-family:"Slabo 13px", serif;text-decoration:none;color:white;font-size:22px;box-shadow:0px 4px 4px rgba(0,0,0,0.25);transition:0.1s}.introduction .buttons a:hover{background:rgba(188,188,188,0.16)}.my-skills-wrapper{font-family:"Slabo 13px", serif}.my-skills-wrapper h1{font-size:48px;font-family:"Slabo 13px", serif;text-align:center;margin-bottom:2vh;color:white;margin-top:8vh}.my-skills-wrapper .skill-components{display:flex;margin:auto auto;width:90%;flex-wrap:wrap;justify-content:space-evenly}.my-skills-wrapper .skill-components article{background-color:#c7c7c7;padding:20px;text-align:center;overflow:hidden;display:flex;flex-direction:column;align-items:center;border:3px solid #000000;height:300px;width:400px;margin:20px;box-shadow:10px 15px 10px #2b2a2a}.my-skills-wrapper .skill-components article img{height:100px;width:100px;margin-top:15px;margin-bottom:15px}.my-skills-wrapper .skill-components article:hover{border:3px solid #ef7c1a;box-shadow:10px 15px 10px #ef7d1a63}.navigation-wrapper{margin-top:10px;display:flex;align-items:center;justify-content:space-evenly;color:white;font-family:"Slabo 13px", serif}.navigation-wrapper h1{font-family:monospace;font-size:35px}.navigation-wrapper .link-wrapper{width:60%;display:flex;justify-content:space-around;align-content:baseline}.navigation-wrapper .link-wrapper a{color:white;text-decoration:none;font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif;font-size:20px;padding-top:50px;padding:50px 10px 10px 10px;margin-bottom:40px;transition:0.4s}.navigation-wrapper .link-wrapper a:hover{background-color:#ef7c1a}.footer-wrapper{bottom:0;left:0;width:100%;position:-webkit-sticky;position:sticky;background-color:rgba(47,48,56,0.6);text-align:center;color:white;margin-top:5vh;font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif}

